@import
'../../../styles/utils/variables',
'../../../styles/utils/mixins';

.container {
  width: 600px;
  height: 250px;

  display: flex;
  flex-direction: column;

  background-color: #ffffff;
  border-radius: 8px;
  border: 1px solid #A1C0E0;
  box-shadow: 4px 10px 2px rgba(42, 47, 66, 0.15);

  font-family: 'Roboto', sans-serif;

  z-index: 2;
  overflow: auto;
  overflow-x: hidden;

  @include respond-to('large') {
    width: 470px;
    height: 250px;
  }

  @include respond-to('medium') {
    width: 380px;
    height: 180px;
  }

  @include darkTheme {
    background: $black-blue;
  }
}

.header {
  width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;

  padding: 30px 10px 0 0;

  @include respond-to('small') {
    padding: 20px 20px 0 0;
  }
}

.header_text {
  margin-top: 15px;
  margin-left: 45px;
  margin-right: 45px;

  font-size: $header;
  line-height: 40px;
  color: #0f3971;

  @include respond-to('large') {
    font-size: $headerLarge;
  }

  @include respond-to('medium') {
    margin-top: 10px;
    margin-left: 30px;

    font-size: $headerMedium;
    line-height: 19px;
  }

  @include darkTheme {
    color: $grey;
  }
}

.close_icon {
  margin-right: 30px;
  width: 24px;
  height: 24px;

  cursor: pointer;

  @include respond-to('medium') {
    width: 14px;
    height: 14px;
  }
}

.data_container {
  font-weight: 300;

  margin-top: 22px;

  padding-right: 45px;
  padding-left: 45px;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  @include respond-to('medium') {
    padding-right: 30px;
    padding-left: 30px;

    font-size: 10px;
  }

  @include darkTheme {
    color: $white;
  }
}

.data_action_button {
  margin-top: 50px;
  margin-bottom: 15px;
  width: 180px;
  height: 45px;

  line-height: 24px;

  @include respond-to('medium') {
    margin-top: 30px;
    width: 110px;
    height: 30px;

    line-height: 20px;
  }
}

.data_action_button span {
  font-size: 16px;
  line-height: 14px;

  @include respond-to('medium') {
    font-size: 11px;
  }
}

.data_button_container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;

  padding-left: 45px;
  padding-right: 45px;

  @include respond-to('medium') {
    padding-left: 30px;
    padding-right: 30px;
  }
}

.radio {
  display: flex;
  margin-top: 15px;
  * {
    margin-left: 23px;
    display: flex;
    align-items: center;

    @include respond-to('medium') {
      margin-left: 15px;
    }
  }

  span {
    margin: 0 5px;
    color: #45474B;
    font-family: $roboto;
    font-size: 20px;

    @include respond-to('medium') {
      font-size: 13px;
    }

    @include darkTheme {
      color: $moderate-blue;
    }
  }

  svg {
    color: #064FA3;

    @include darkTheme {
      color: $moderate-blue;
    }
  }
}